/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useEffect, useRef } from 'react'
import Particles from "react-tsparticles"; 
import { loadFull } from "tsparticles"; 
import $ from 'jquery'
import Typed from 'typed.js';

export default function Home() {



  
  const el = useRef(null);

  useEffect(() => {





    
    $('.loading').fadeOut(1000)

    const typed = new Typed(el.current, {
      strings: ['Front-End developer!', 'Back-End Developer'],
      typeSpeed: 50,
      backSpeed: 70,
      loop: true,
      loopCount: Infinity,

    });

    return () => {
      typed.destroy();
    };
  }, []);



  const particlesInit = async (main) => { 
    console.log(main); 
  
    await loadFull(main); 
  }; 


  return <>
  <div id='home' className='vh-100'>
  <div className="loading position-fixed top-0 bottom-0 opacity-100 z-2 end-0 start-0 ">
      <i className="fa-solid fa-spider fa-spin text-info fa-5x "></i>
    </div>

  <div className='container-fluid d-flex align-items-center vh-100' id='home'>
    <div className="container">
    <div className="row align-items-center">

            <div className="col-md-6 " >
                <div className="descrption">
                  <h2 className='fw-bold fs-5 py-5' data-aos="fade-down" data-aos-duration="3000">Hello It's me</h2>
                  <h1 data-aos="fade-up" data-aos-duration="3000" className='fw-bolder fs-1'  >Ahmed Hossam Ahmed</h1>
                  <p className='fw-border fs-3 py-3' >And I'm a <span  className='fw-bold' ref={el} /></p>
        
                  <div className='py-2 d-flex justify-align-content-around'>


                    <a data-aos="fade-right" data-aos-duration="3000" className='text-white fw-bold'  href="https://drive.google.com/file/d/1U6V2pmNNdbLNW2QW0McUd1EqK1URX_SE/view?usp=drive_link"> Download CV</a>
                    <a data-aos="fade-left" data-aos-duration="3000" className='text-white mx-3 fw-bold ' href="#">let's talk</a>

                  </div>
                  
                </div>
            </div>
        </div>
    </div>
      </div>












{/*  */}




  <div className="App">

      <Particles 
      id="tsparticles"
      init={particlesInit}

      options={{
        "fullScreen": {
            "enable": true,
            "zIndex": -1
        },
        fpsLimit: 60,
  particles: {
    groups: {
      z5000: {
        number: {
          value: 70
        },
        zIndex: {
          value: 5000
        }
      },
      z7500: {
        number: {
          value: 30
        },
        zIndex: {
          value: 75
        }
      },
      z2500: {
        number: {
          value: 50
        },
        zIndex: {
          value: 25
        }
      },
      z1000: {
        number: {
          value: 40
        },
        zIndex: {
          value: 10
        }
      }
    },
    number: {
      value: 200,
      density: {
        enable: false,
        value_area: 800
      }
    },
    color: {
      value: "#fff",
      animation: {
        enable: false,
        speed: 20,
        sync: true
      }
    },
    shape: {
      type: "circle"
    },
    opacity: {
      value: 1,
      random: false,
      animation: {
        enable: false,
        speed: 3,
        minimumValue: 0.1,
        sync: false
      }
    },
    size: {
      value: 3
    },
    links: {
      enable: false,
      distance: 100,
      color: "#ffffff",
      opacity: 0.4,
      width: 1
    },
    move: {
      angle: {
        value: 10,
        offset: 0
      },
      enable: true,
      speed: 5,
      direction: "right",
      random: false,
      straight: true,
      outModes: {
        default: "out"
      },
      attract: {
        enable: false,
        rotateX: 600,
        rotateY: 1200
      }
    },
    zIndex: {
      value: 5,
      opacityRate: 0.5
    }
  },
  interactivity: {
    detectsOn: "canvas",
    events: {
      onHover: {
        enable: false,
        mode: "repulse"
      },
      onClick: {
        enable: true,
        mode: "push"
      },
      resize: true
    },
    modes: {
      grab: {
        distance: 400,
        links: {
          opacity: 1
        }
      },
      bubble: {
        distance: 400,
        size: 40,
        duration: 2,
        opacity: 0.8
      },
      repulse: {
        distance: 200
      },
      push: {
        quantity: 4,
        groups: ["z5000", "z7500", "z2500", "z1000"]
      },
      remove: {
        quantity: 2
      }
    }
  },
  detectRetina: true,
  emitters: {
    position: {
      y: 55,
      x: -30
    },
    rate: {
      delay: 7,
      quantity: 1
    },
    size: {
      width: 0,
      height: 0
    },
    particles: {
      shape: {
        type: "images",
        options: {
          images: [
            {
              src: "https://particles.js.org/images/amongus_blue.png",
              width: 205,
              height: 267
            },
            {
              src: "https://particles.js.org/images/amongus_cyan.png",
              width: 207,
              height: 265
            },
            {
              src: "https://particles.js.org/images/amongus_green.png",
              width: 204,
              height: 266
            },
            {
              src: "https://particles.js.org/images/amongus_lime.png",
              width: 206,
              height: 267
            },
            {
              src: "https://particles.js.org/images/amongus_orange.png",
              width: 205,
              height: 265
            },
            {
              src: "https://particles.js.org/images/amongus_pink.png",
              width: 205,
              height: 265
            },
            {
              src: "https://particles.js.org/images/amongus_red.png",
              width: 204,
              height: 267
            },
            {
              src: "https://particles.js.org/images/amongus_white.png",
              width: 205,
              height: 267
            }
          ]
        }
      },
      size: {
        value: 40
      },
      move: {
        speed: 10,
        outModes: {
          default: "destroy",
          left: "none"
        },
        straight: true
      },
      zIndex: {
        value: 0
      },
      rotate: {
        value: {
          min: 0,
          max: 360
        },
        animation: {
          enable: true,
          speed: 10,
          sync: true
        }
      }
    }
  }
      
      
    }}
    />
    </div>

    </div> 


  </>
}
